<template>
  <div id="app">
    <div class="app-phone">
      <div class="phone-header">
        <a class="cancel-cta" @click="goToHome" v-if="step === 2 || step === 3">Cancel</a>
        <img src="./assets/vue_gram_logo_cp.png">
        <a class="next-cta" v-if="step === 2" @click="step ++">Next</a>
        <a class="next-cta" v-if="step === 3" @click="Share">Share</a>
      </div>
      <PhoneBody :PhoneProps="PhoneProps" :step="step" :Image="Image" :Filters="Filters" :FilterName="FilterName" v-model="caption"></PhoneBody>
      <div class="phone-footer">
        <div class="home-cta">
          <i class="fas fa-home fa-lg" @click="goToHome"></i>
        </div>
        <div class="upload-cta">
          <input type="file" name="file" id="file" @change="PostChange" :disabled="step != 1">
          <label for="file">
            <i class="far fa-plus-square fa-lg"></i>
          </label>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import PhoneBody from "./components/PhoneBody.vue";
import PhoneProps from "./data/posts.js";
import Filters from "./data/filters.js";
import EventBus from "./EventBus.js";
export default {
  name: "app",
  data() {
    return {
      PhoneProps,
      step: 1,
      Image: "",
      Filters,
      FilterName: '',
      caption: '',
    };
  },
  created(){
    EventBus.$on('FilterNameChange', data=>{
      // console.log(data);
      this.FilterName = data;
    })
  },
  components: {
    PhoneBody
  },
  methods: {
    PostChange(e) {
      // console.log(e.target.files);
      if (e.target.files.length == 0) {
        return;
      } else if(!/\.(gif|jpg|jpeg|png|GIF|JPG|PNG)$/.test(e.target.files[0].name)){
        alert("ËØ∑‰∏ä‰º†ÂõæÁâáÊñá‰ª∂");
        return;
      }else{
        const Reader = new FileReader();
        Reader.readAsDataURL(e.target.files[0]);
        Reader.onload = e => {
          this.step = 2;
          this.Image = e.target.result;
        };
      }
    },
    goToHome(){
      this.step = 1;
      this.Image = '';
      this.FilterName = '';
    },
    Share(){
      const post = {
        username: "liuluwei",
        userImage: "https://s3-us-west-2.amazonaws.com/s.cdpn.io/1211695/me_3.jpg",
        postImage:this.Image,
        likes: 0,
        hasBeenLiked: false,
        caption: this.caption,
        filter: this.FilterName
      };
      PhoneProps.unshift(post);
      this.goToHome();
    }
  }
};
</script>

<style lang="scss" src="./styles/app.scss">
</style>
