<template>
  <div class="phone-body">
    <div class="feed" v-if="step === 1" v-dragscroll>
      <VuegramPost 
      v-for="(post,index) in PhoneProps" 
      :key="index" :post="post"
      ></VuegramPost>
    </div>
    <div v-if="step === 2" >
      <div class="selected-image" 
      :style="{backgroundImage:'url('+ Image +')'}"
      :class="FilterName"
      ></div>
      <div class="filter-container"  v-dragscroll>
          <FilterUpdate 
          :Image="Image"
          v-for="(filter,index) in Filters"
          :key="index"
          :filter="filter"
          ></FilterUpdate>
      </div> 
    </div>
    <div v-if="step === 3">
      <div class="selected-image" 
      :style="{backgroundImage:'url('+ Image +')'}"
      :class="FilterName"
      ></div>
      <div class="caption-container">
        <textarea 
          placeholder="Write a caption..."
          @input="captionChange"
        ></textarea>
      </div>
    </div>
  </div>
</template>
<script>
import VuegramPost from "./vueGrampost.vue";
import FilterUpdate from "./Filterupdate.vue";

export default {
  name: "PhoneBody",
  props: {
    PhoneProps: Array,
    Image: String,
    step: Number,
    Filters: Array,
    FilterName: String,
  },
  components: {
    VuegramPost,
    FilterUpdate
  },
  methods:{
    captionChange(e){
      // console.log(e)
      this.$emit("input", e.target.value)
    }
  }
};
</script>
<style lang="scss" src = '../styles/phone-body.scss' >
</style>

